#next-button {
    cursor: pointer;
    padding: .5rem 2rem;
    font-size: 1rem;
    border-radius: 2rem;
    color: #50c6db;
    background-color: #fff;
    text-transform: uppercase;
    font-weight: bold;
    border: 2px solid #fff;
}

#next-button.skip {
    color: #fff;
    background-color: rgba(0,0,0,.2);
}

.button-container {
    width: 100%;
    -ms-flex: 1 0 100%;
        flex: 1 0 100%;
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-align: center;
        align-items: center;
}




