.winner {
    background: yellow;
}

.hooray {
    -webkit-animation: hooray .4s cubic-bezier(0.67,-0.08, 0.01, 1.32) forwards;
            animation: hooray .4s cubic-bezier(0.67,-0.08, 0.01, 1.32) forwards;
}

.answer-container {
    text-align: center;
    width: 100%;
}

.answer-container input, .answer-container p {
    text-shadow: 0px 0px 2em rgba(55,55,55,0.1);
}

.answer-input {
    color: #fff;
    text-align: center;
    background: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    padding: .5rem 0;
    font-size: 1rem;
    border: none;
    border-bottom: 2px solid #fff;
}

.answer-input.warmer {
    font-weight: 700;
    color: #fefbea;
}

.answer-input.colder {
    color: #ef4e65;
}

.answer-input:focus {
    outline: none;
}

#answer-reveal-container {
    margin: .5rem 0 2rem;
    min-height: 2.7rem;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-line-pack: justify;
        align-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

#answer-reveal-container p {
    margin: 0;
    width: 100%;
}

@-webkit-keyframes hooray {
    0% {
        -webkit-transform: scale(.7);
                transform: scale(.7);
    }
    40% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    65% {
        -webkit-transform: scale(0.9);
                transform: scale(0.9);
    }
    85% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes hooray {
    0% {
        -webkit-transform: scale(.7);
                transform: scale(.7);
    }
    40% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    65% {
        -webkit-transform: scale(0.9);
                transform: scale(0.9);
    }
    85% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}


